@import "../common/styles/sass/variables";


.projectsBlock {
    min-height: 100vh;
    background: $bgSecondColor;
    .projectsContainer {
        flex-direction: column;
        justify-content: flex-start;
        .projects {
            width: 100%;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;

            @media screen and (max-width: 1133px) {
                justify-content: center;
            }
        }
    }
}