@import "../../../common/styles/sass/mixins";
@import "../../../common/styles/sass/variables";


.title {
  margin-bottom: 35px;
  position: relative;
  margin-top: 30px;

  h2 {
    @include projectTitle(28px, 30px);
    letter-spacing: 1px;
    text-transform: uppercase;
    margin: 0;
    box-sizing: border-box;
    text-align: center;
  }

  &::before {
    content: "";
    background: #232323 none repeat scroll 0 0;
    bottom: -15px;
    height: 2px;
    left: 50%;
    position: absolute;
    top: auto;
    transform: translateY(0px) translateX(-50%);
    width: 75px;
  }
}




